*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-boxm;
}
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
body {
  font-family: sans-serif;
}
.logger-app {
  text-align: center;
  font-family: sans-serif;
}
.search {
  display: flex;
  width: 100%;
  padding: 1rem 1rem;
  background-color: dodgerblue;
  align-items: center;
}
.search i {
  font-size: 1.6rem;
  margin-right: 0.4rem;
  color: #fff;
}
.search input {
  flex-grow: 1;
  padding: 0.5rem 1rem;
  border: none;
  background-color: dodgerblue;
}
.search input:focus {
  background-color: #fff;
  border: none;
  outline: 0;
}
.search input::placeholder {
  color: #fff;
}
.container {
  padding-top: 1.5rem;
  max-width: 1000px;
  margin: 0 auto;
}
/* logs */

.log-item {
  display: flex;
  padding: 0.7rem;
  border-bottom: 1px solid #ccc;
}
.log-detail,
.log-options {
  display: flex;
  flex-direction: column;
}

.log-item h3 {
  margin-bottom: 0.6rem;
  color: dodgerblue;
  text-align: left;
}
.log-item h3.attention {
  color: red;
}

.log-options {
  margin-left: auto;
  font-size: 1.1rem;
  cursor: pointer;
}
.log-options *:first-child {
  margin-bottom: 0.3rem;
}
.log-detail p {
  display: flex;
}
.log-detail p span {
  color: gray;
  margin: 0 0.5rem;
}
.log-options .trash:hover {
  color: red;
}
.log-options .edit:hover {
  color: rgb(5, 129, 5);
}

/* add btn */
.add-btn {
  position: fixed;
  bottom: 80px;
  right: 80px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: navy;
  color: #fff;
  font-size: 1.1rem;
}
.add-btn .add-log-btn,
.add-btn .add-tech-btn,
.add-btn .tech-list-btn {
  cursor: pointer;
}
.add-btn .add-tech-btn,
.add-btn .tech-list-btn {
  position: absolute;
  left: 50%;
  bottom: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transform: translate(-50%, 0);
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}
.add-btn .tech-list-btn i,
.add-btn .add-tech-btn i {
  position: absolute;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-btn .tech-list-btn {
  transform: translate(-50%, -100%);
}
.add-btn .tech-list-btn i {
  background-color: green;
}
.add-btn .add-tech-btn i {
  background-color: orangered;
}
.add-btn:hover .add-tech-btn,
.add-btn:hover .tech-list-btn {
  opacity: 1;
  visibility: visible;
}
/* modal */
.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal form,
.modal .tech-card {
  background-color: #fff;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  color: black;
  width: 70%;
  max-width: 800px;
}
.modal form h2,
.modal .tech-card h2 {
  margin-bottom: 1rem;
}
.modal form input[type="text"] {
  height: 1rem;
  padding: 0.7rem;
}
.modal form select {
  height: 2rem;
}
.attention-row {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}
.attention-row input {
  margin-left: 0.4rem;
}
.modal form button {
  background-color: dodgerblue;
  margin-top: 1rem;
  color: #fff;
  border: none;
  height: 2rem;
  cursor: pointer;
}

/* Technecian */
.modal .tech-card {
  background-color: rgb(231, 231, 231);
}
.techs-list {
  background-color: #fff;
}

.addTech input {
  margin-bottom: 1rem;
  border: none;
  border-bottom: 1px solid #ccc;
}
